









































import Vue from "vue";
import Component from "vue-class-component";
import { Login } from "@/api";

@Component
export default class extends Vue {
  private username = "";
  private password = "";

  private async onLoginFormHandler(e: Event) {
    e.preventDefault();
    const loginData = await Login.logar(this.username, this.password);
    if (!loginData) {
      alert("Login/Senha invalidos.");
      return;
    }

    localStorage.setItem("user_data", JSON.stringify(loginData));
    this.$router.push("/admin");
  }
}
